body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  height: 100vh;
  width: 100vw;
  background: black;
}
.app {
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;
}
.contentContainer {
  position: relative;
  margin: 0;
  background: rgba(255, 255, 255, 0.1);
  color: white;
  text-align: center;
}
span {
  font-family: copperplate;
}
h1 {
  font-size: 40px;
  font-family: copperplate;
}
h2 {
  margin: 20px;
  font-size: 30px;
  font-family: copperplate;
}
p, a {
  color: white;
  font-size: 26px;
  font-family: copperplate;
}

@media (max-width: 1024px) {
  h1 {
    font-size: 3vw;
  }
  h2 {
    font-size: 3vw;
  }
  p, a {
    font-size: 3vw;
  }
}
.navBarContainer {
	height: 8vh;
	width: 100vw;
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-items: center;
	background: black;
	position: fixed;
	z-index: 100;
	border-bottom: 1px solid rgba(255,255,255,0.5);
}

.logo {
	height: 6vh;
	width: auto;
	border-radius: 10px;
	border: 3px solid white;
	align-self: center;
	background: rgba(255, 255, 255, 0.1);
}
.desktopNav {
	margin-top: 0px;
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	height: 8vh;
	width: 80vw;
}
.desktopNav ul{
	margin: 0;
	height: 8vh;
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-items: center;
}
.desktopNav ul a {
	display: flex;
	height: 8vh;
	justify-content: center;
	align-items: center;
	cursor: pointer;
	margin: 0 auto;
	list-style-type: none;
	padding: 0 20px 0 20px;
	color: white;
	text-decoration: none;
	transition: 0.3s;
}
.navBarContainer li { 
	transition: 0.3s;
 }
.navBarContainer nav ul a:hover {
	background: rgba(255, 255, 255, 0.2);
	transition: 0.3s;
}

@media (max-width: 650px) {
	.desktopNav ul a {
		padding: 0 1vw 0 1vw;
	}
}
@media (max-width: 550px) {

}

#technologiesContainer {
	position: relative;
	display: flex;
	flex-direction: column;
	justify-content: center;
	width: 100%;
	background: #222222;
}
.technologiesContainer h1 {
	z-index: 20;
}
#technologies {
	display: flex;
	flex-direction: row;
	justify-content: center;
	flex-wrap: wrap;
}
.headerAnchored {
	position: absolute;
	top: 0%;
	left: 40%;
	margin-top: 0;
}
.card {
	display: flex;
	flex-direction: column;
	align-items: center;
	background: #222222;
	width: 9vw;
	border-radius: 10px;
	margin: 10px;
	transition: position 3s;
}
.card h2 {
	font-size: 24px;
}
.card img {
	width: 90%;
}
@media (max-width: 1024px) {
	.card h2 {
		margin: 10px 0 10px 0;
		font-size: 2vw;
	}
}
.aboutMe {
	margin-top: 8vh;
	width: 100vw;
	text-align: center;
	color: white;
}
.parallaxContainer {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	height: 90vh;
	width: 100vw;
	background: url(/static/media/IMG_0994.a4299fb3.JPG) no-repeat fixed center center;
	background-size: cover;
}
.parallaxText {
	color: white;
	font-size: 4vw;
	font-weight: bold;
}
.paragraphContainer {
	border-top: 1px solid rgba(255,255,255,0.5);
	border-bottom: 1px solid rgba(255,255,255,0.5);
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	flex: wrap 1;
	background: linear-gradient(to right, black 55%, #232323 75%, #232323 100%);

}
.paragraphContainer img {
	height: 50vw;
	align-self: flex-end;
}
.textContainer {
	margin: 0 10vw;
}
.textContainer span {
	margin-top: 2000px;
}
.underlined {
	padding: 0px 20px 0px 20px;
	border-bottom: 1px solid white;
}
@media (max-width: 1024px) {
	.paragraphContainer {
		flex-direction: column;
		justify-content: space-between;
		align-items: center;
		background: linear-gradient(black 25%, #232323 45%, #232323 100%);
	}
	.paragraphContainer img {
		align-self: center;
	}
}
.portfolio {
	margin-top: 10vh;
}
.projectsContainer {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	justify-content: center;
	margin: 40px;
}
.githubLink {
	border: 3px solid white;
	border-radius: 100%;
	height: 60px;
	width: 60px;
	cursor: pointer;
	transition: 0.3s;
}
.githubLink:hover {
	transition: 0.3s;
	background: white;
}
.projects {
	position: relative;
	height: 300px;
	width: 300px;
	border-radius: 20px;
	border: 3px solid white;
	overflow: hidden;
	margin: 0px 60px 40px;
}
.phoneSummary {
	display: none;
}
.robofriends {
	background-image: url(/static/media/Robofriends.021bb939.png);
	background-size: cover;
	background-position: center center;
	background-repeat: no-repeat;
	height: 300px;
	width: 300px;
	border-radius: 20px;
}
.smartBrain {
	background-image: url(/static/media/Smartbrain.f8c7963f.png);
	background-size: cover;
	background-position: center center;
	background-repeat: no-repeat;
	height: 300px;
	width: 300px;
	border-radius: 20px;
}
.resumeSite {
	background-image: url(/static/media/ResumeSite.749302ca.PNG);
	background-size: cover;
	background-position: center center;
	background-repeat: no-repeat;
	height: 300px;
	width: 300px;
	border-radius: 20px;
}
.rainbowAngles {
	background-image: url(/static/media/RainbowAngles.a004f4e0.PNG);
	background-size: cover;
	background-position: center center;
	background-repeat: no-repeat;
	height: 300px;
	width: 300px;
	border-radius: 20px;	
}
.shadowBox {
	z-index: 2;
	background: rgba(0,0,0,0);
	color: rgba(0,0,0,0);
	position: absolute;
	top: 0;
	left: 0;
	height: 300px;
	width: 300px;
	border-radius: 20px;
	transition: 0.3s;
}
.shadowBox p {
	margin: 0 10px;
	position: relative;
	top: 50%;
	-webkit-transform: perspective(1px) translateY(-50%);
	        transform: perspective(1px) translateY(-50%);
}
.shadowBox:hover {
	z-index: 2;
	transition: 0.3s;
	background: rgba(0, 0, 0,0.9);
	display: inline-block;
	vertical-align: middle;
	color: white;
	height: 300px;
	width: 300px;
}
@media (max-width: 1332px) {
	.projects {
		margin: 0px 20px 40px;
	}
}
@media (max-width: 920px) {
	.projectsContainer {
		flex-direction: column;
	}
	.projects {
		margin: 0px 20px 20px;
		height: 200px;
		width: 200px;		
	}
	.projectBlockContainer {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
	}
	.phoneSummary {
		display: inline-block;
		float: right;
		color: white;
		align-self: ;
	}
	.shadowBox {
		display: none;
	}
	.robofriends {
		height: 200px;
		width: 200px;
	}
	.smartBrain {
		height: 200px;
		width: 200px;
	}
	.resumeSite {
		height: 200px;
		width: 200px;
	}
	.rainbowAngles {
		height: 200px;
		width: 200px;
	}
}
@media  (max-width: 480px) {
	.projectsContainer {
		margin: 0;
	}
	.projects {
		margin: 10px;
		height: 150px;
		width: 150px;
	}
	.robofriends {
		height: 150px;
		width: 150px;
	}
	.smartBrain {
		height: 150px;
		width: 150px;
	}
	.resumeSite {
		height: 150px;
		width: 150px;
	}
	.rainbowAngles {
		height: 150px;
		width: 150px;
	}
}
.contactMe {
	display: flex;
	flex-direction: row;
	justify-content: flex-start;
	background: black;
	color: white;
	border-top: 1px solid rgba(255,255,255,0.5);
}
.contactContainer {
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	margin-left: 10vw;
	margin-bottom: 20px;
}
.contactMe p, .contactMe a {
	margin-left: 10px;
}
.contactInfo {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	justify-content: center;
	align-items: center;
}
.contactInfo p, .contactInfo h2 {
	margin: 5px;
}
@media (max-width: 700px) {

}
